@font-face {
  font-family: 'PressStart';
  src: local('PressStart'), url(./fonts/PressStart2P-Regular.ttf) format('truetype');
}

.app{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'PressStart';
  height: 100vh;

}

.grid-row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.grid-cell{
  width: 15px;
  height: 15px;
  /* border-top: 1px solid black;
  border-left: 1px solid black; */
}

.grid-cell-border{
  background-color: black;
}

.grid-cell-snake{
  background-color: black;
}

.grid-cell-snack{
  background-color: blue;
}

.grid-cell-hit {
  background-color: red;
}

.gameover{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 400px;
  height: 400px;
}

.typing{
  font-size: 30px;
}

.gameover .typing{
  width: 400px;
  text-align: center;
  color: red;
}

.again{
  font-size: 14px;
  cursor: pointer;
  margin-top: 30px;
}

.Typist .Cursor {
  display: inline-block;
}

.Typist .Cursor--blinking {
  opacity: 1;
  -webkit-animation: blink 1s linear infinite;
          animation: blink 1s linear infinite;
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



